<template>
  <div class="home">
    <l-map
      ref="map"
      class="map"
      :zoom="zoom"
      :center="center"
      @ready="() => {this.map = this.$refs.map.mapObject}"
      @update:zoom="(zoom) => {this.zoom = zoom}"
      @update:center="(center) => {this.center = center}"
      @update:bounds="(bounds) => {this.bounds = bounds}"
    >
      <l-tile-layer
        v-show="layers.tiles"
        :url="url"
      />
      <l-geo-json
        v-if="layers.railAreas"
        :geojson="require('@/assets/railAreas.json')"
        :options="options"
      />
      <l-marker class="markerBlock" v-for="(marker, i) in markers" :key="i" :lat-lng="marker.latLng">
        <l-icon :icon-anchor="[40, 25]">
          <v-sheet
            v-show="zoom >= 4"
            shaped
            color="white"
            elevation="4"
            width="90"
            style="overflow: hidden"
          >
            <v-progress-linear
              :value="marker.public.empty / marker.public.total * 100"
              color="red"
              height="25"
            >
              <strong>о - {{ marker.public.empty }}/{{ marker.public.total }}</strong>
            </v-progress-linear>
            <v-progress-linear
              :value="marker.private.empty / marker.private.total * 100"
              color="blue-grey"
              height="25"
            >
              <strong>н - {{ marker.private.empty }}/{{ marker.private.total }}</strong>
            </v-progress-linear>
          </v-sheet>
        </l-icon>
      </l-marker>
    </l-map>
  </div>
</template>

<script>
// @ is an alias to /src
import { LMap, LTileLayer, LGeoJson, LMarker, LIcon } from 'vue2-leaflet'

export default {
  name: 'Home',
  components: {
    LMap,
    LTileLayer,
    LGeoJson,
    LMarker,
    LIcon
  },

  data: () => ({
    map: null,
    zoom: 4,
    center: [60.703, 83.214],
    bounds: null,
    url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
    layers: {
      railAreas: true
    },
    markers: [
      // {
      //   latLng: [60.703, 83.214],
      //   public: {
      //     total: 1000,
      //     empty: 800
      //   },
      //   private: {
      //     total: 1000,
      //     empty: 500
      //   }
      // }
    ]
  }),

  methods: {
    codeToColor (code) {
      switch (code) {
        case 10:
        case 24:
        case 61:
        case 83:
        case 96:
          return '#525252'
        case 28:
        case 41:
        case 63:
        case 88:
          return '#969696'
        case 17:
        case 51:
        case 80:
        case 92:
          return '#cccccc'
        case 1:
        case 58:
        case 76:
        case 94:
          return '#f7f7f7'
        default:
          return 'white'
      }
    }
  },

  computed: {
    options () {
      return {
        onEachFeature: this.onEachFeatureFunction
      }
    },
    onEachFeatureFunction () {
      return (feature, layer) => {
        this.markers.push({
          latLng: layer.getBounds().getCenter(),
          public: {
            total: feature.properties.code * 100,
            empty: Math.floor(feature.properties.code * 100 * Math.random())
          },
          private: {
            total: feature.properties.code * 10,
            empty: Math.floor(feature.properties.code * 10 * Math.random())
          }
        })
        layer.bindTooltip(
          '<div>code:' +
          feature.properties.code +
          '</div><div>name: ' +
          feature.properties.name +
          '</div>',
          { permanent: false, sticky: true }
        )
        layer.setStyle({
          weight: 1.5,
          color: '#5a5a5a',
          opacity: 1,
          fillColor: this.codeToColor(feature.properties.code),
          fillOpacity: 0.5
        })
      }
    }
  }
}
</script>

